<template>
  <div class="about">
    <h1>This is an about page</h1>
    <div class="douban">
        <ul>
            <li v-for="item in dataList">
                <!-- <img :src="item.images.medium" alt=""> -->
                <div :style="{backgroundImage: 'url(' + item.images.medium + ')'}"></div>
                <p class="dbtitle">{{item.title}}</p>
                <p class="displaynone">{{item.casts[0].name}},{{item.casts[1].name}},{{item.casts[2].name}}</p>
                <p class="displaynone dbyear fl">
                    <span class="fl">{{item.year}}</span>
                    <span class="fr">{{item.genres[0]}},{{item.genres[1]}}</span>
                </p>
                <p class="dbaver">豆瓣评分：{{item.rating.average}}</p>
            </li>
        </ul>
    </div>
  </div>
</template>
<script>
export default {
  name:"about",
  data(){
    return {
      dataList:[],
    }
  },
  created(){
      var url=this.HOST+"/movie/top250"
      this.$axios.post(url,{
          count:10,start:1
      })
      .then(res =>{
          this.dataList = res.data.subjects;
          console.log(res.data)
      })
      .catch(error =>{
            console.log(error)
      })
  }
}
</script>
<style scoped>
ul li{list-style: none;}
</style>

